<template>
  <div class="mainBox">
    <div class="el-row fileManage systemMonitor">
      <div class="el-col el-col-24 page_content_top">
        <div class="left pull-left">
          <el-select v-model="selectNode" @change="selectChange">
            <el-option
              v-for="item in nodeListInfo"
              :key="item.id"
              :label="item.host"
              :value="item.id"
            ></el-option>
          </el-select>
        </div>
        <div class="right pull-right">
          <el-tooltip class="item" content="" placement="bottom">
            <span class="refresh">
              <span class="icon-refresh" @click="refreshOperate()"></span>
            </span>
          </el-tooltip>
        </div>
      </div>
      <div class="el-col el-col-24 page_content_cont">
        <el-card class="box-card card-top">
          <div class="CPU-coreNum">
            <h4 class="title-cont">{{cpuCores}}</h4>
            <span class="title">CPU</span>
          </div>
          <div class="CPU-brand">
            <h4 class="title-cont">{{cpuBrand}}</h4>
            <span class="title">CPU</span>
          </div>
          <div class="memorySize">
            <h4 class="title-cont">
              {{memoryTotalBytes}}
              <span class="gb">GB</span>
            </h4>
            <span class="title"></span>
          </div>
          <div class="operatingSystem">
            <h4 class="title-cont">{{osDesc}}</h4>
            <span class="title"></span>
          </div>
        </el-card>
        <div class="card-box">
          <el-row :gutter="20">
            <!-- CPU -->
            <el-col :span="12">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="cardTitle">CPU</span>
                </div>
                <div class="chart-box">
                  <div class="chart-cont">
                    <v-echarts
                      ref="cpuChart"
                      :echartsId="cupChart"
                      :seriesParam="cpuSeriesParam"
                      :yAxisName="cpuYName"
                      :xAxisType="xType"
                      :useAxis="true"
                      :yAxisNameLocation="yAxisNameLocation"
                      :axisLabelFormatter="axisLabelFormatter"
                      :gridLeft="gridLeft"
                      :yNameGap="90"
                    ></v-echarts>
                  </div>
                </div>
              </el-card>
            </el-col>
            <!-- /SWAP -->
            <el-col :span="12">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="cardTitle">/SWAP</span>
                </div>
                <div class="chart-box">
                  <div class="chart-cont">
                    <v-echarts
                      ref="memChart"
                      :echartsId="memChart"
                      :seriesParam="memSeriesParam"
                      :yAxisName="memYName"
                      :xAxisType="xType"
                      :useAxis="true"
                      :yAxisNameLocation="yAxisNameLocation"
                      :axisLabelFormatter="axisLabelFormatter"
                      :gridLeft="gridLeft"
                      :yNameGap="35"
                    ></v-echarts>
                  </div>
                </div>
              </el-card>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <!--  -->
            <el-col :span="12">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="cardTitle"></span>
                </div>
                <div class="chart-box">
                  <el-select v-model="selectDiskusage" @change="diskusageInit()">
                    <el-option
                      v-for="item in diskusageName"
                      :key="item"
                      :label="item"
                      :value="item"
                    ></el-option>
                  </el-select>
                  <div class="chart-cont">
                    <v-echarts
                      ref="diskChart"
                      :echartsId="diskChart"
                      :yAxisName="diskYName"
                      :xAxisType="xType"
                      :useAxis="true"
                      :yAxisNameLocation="yAxisNameLocation"
                      :axisLabelFormatter="axisLabelFormatter"
                      :seriesName="diskSeriesName"
                      :seriesData="diskSeriesParam"
                      :gridLeft="gridLeft"
                      :yNameGap="35"
                    ></v-echarts>
                  </div>
                </div>
              </el-card>
            </el-col>
            <!-- I/O -->
            <el-col :span="12">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="cardTitle">I/O</span>
                </div>
                <div class="chart-box">
                  <el-select v-model="selectDiskio" @change="diskIOInit()">
                    <el-option v-for="item in diskioName" :key="item" :label="item" :value="item"></el-option>
                  </el-select>
                  <div class="chart-cont">
                    <v-echarts
                      ref="diskIOChart"
                      :echartsId="diskIOChart"
                      :yAxisName="diskIOYName"
                      :xAxisType="xType"
                      :useAxis="true"
                      :yAxisNameLocation="yAxisNameLocation"
                      :axisLabelFormatter="axisLabelFormatter"
                      :seriesName="diskIOSeriesName"
                      :seriesData="diskIOSeriesData"
                      :gridLeft="gridLeft"
                      :yNameGap="40"
                    ></v-echarts>
                  </div>
                </div>
              </el-card>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <!--  -->
            <el-col :span="12">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="cardTitle"></span>
                </div>
                <div class="chart-box">
                  <el-select v-model="selectNet" @change="netInit()">
                    <el-option v-for="item in netName" :key="item" :label="item" :value="item"></el-option>
                  </el-select>
                  <div class="chart-cont">
                    <v-echarts
                      ref="netChart"
                      :echartsId="netChart"
                      :yAxisName="netYName"
                      :xAxisType="xType"
                      :useAxis="true"
                      :yAxisNameLocation="yAxisNameLocation"
                      :axisLabelFormatter="axisLabelFormatter"
                      :seriesParam="netSeriesParam"
                      :gridLeft="gridLeft"
                      :yNameGap="75"
                    ></v-echarts>
                  </div>
                </div>
              </el-card>
            </el-col>
            <!-- Load Average  -->
            <el-col :span="12">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="cardTitle">Load Average</span>
                </div>
                <div class="chart-box">
                  <div class="chart-cont loadChart">
                    <v-echarts
                      ref="loadAverageChart"
                      :echartsId="loadAverageChart"
                      :seriesName="loadAverageSeriesName"
                      :seriesData="loadAverageSeriesData"
                      :yAxisName="loadAverageYName"
                      :xAxisType="xType"
                      :useAxis="true"
                      :yAxisNameLocation="yAxisNameLocation"
                      :axisLabelFormatter="axisLabelFormatter"
                      :gridLeft="gridLeft"
                      :yNameGap="35"
                    ></v-echarts>
                  </div>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Echarts from "../../utils/echartsUtil";
export default {
  name: "systemMonitor",
  components: {
    "v-echarts": Echarts
  },
  data() {
    return {
      selectNode: null, //
      nodeListInfo: [], //
      cpuCores: null, //cpu
      cpuBrand: null, //cpu
      memoryTotalBytes: null, //
      osDesc: null, //
      cupChart: "cupChart", //cupid
      cpuYName: "CPU(%)", //cupY
      cpuSeriesParam: [], //cpu
      yAxisNameLocation: "center", //y
      memChart: "memChart", //id
      memSeriesParam: [], //
      memYName: "/SWAP(GB)", //Y
      diskusageList: null, //
      selectDiskusage: null, //
      diskusageName: [], //
      diskChart: "diskChart", //id
      diskSeriesParam: [], //
      diskYName: "(%)", //y
      diskSeriesName: "(%)",
      diskioList: null, //IO
      diskioName: [], //IO
      selectDiskio: null, //IO
      diskIOChart: "diskIOChart", //IOid
      diskIOYName: "I/O(%)", //IOy
      diskIOSeriesName: "I/O(%)", //
      diskIOSeriesData: [], //IO
      netList: null, //
      netName: [], //
      selectNet: null, //
      netChart: "netChart", //id
      netYName: "(kb/s)",
      netSeriesParam: [],
      loadAverageChart: "loadAverageChart", //id
      loadAverageSeriesName: "Load Average",
      loadAverageSeriesData: [],
      loadAverageYName: "Load Average",
      gridLeft: 30,
      yNameGap: 0, //y
      xType: "time"
    };
  },
  mounted() {
    this.nodeList();
  },
  methods: {
    //
    nodeList() {
      this.$get("sys/nodes", null)
        .then(res => {
          this.nodeListInfo = res;
          this.selectNode = res[0].id;
          this.nodeInfoInit();
        })
        .catch(() => {});
    },
    //
    nodeInfoInit() {
      var list = this.nodeListInfo;
      for (var i in list) {
        if (this.selectNode == list[i].id) {
          this.cpuCores = list[i].cpuCores;
          this.cpuBrand = list[i].cpuBrand;
          this.memoryTotalBytes = parseFloat(
            (list[i].totalMemSize / (1024 * 1024 * 1024)).toFixed(2)
          );
          this.osDesc = list[i].os;
        }
      }
      this.getCpuData();
      this.getMemData();
      this.getDiskusageData();
      this.getDiskioData();
      this.getNetData();
      this.getLoad();
    },
    //
    selectChange() {
      this.initData();
      this.nodeInfoInit();
    },
    //CPU
    getCpuData() {
      var selectNodeId = this.selectNode;
      var param = {
        minutes: 60
      };
      this.$get("sys/cpu/" + selectNodeId, param)
        .then(res => {
          var datas = res;
          let totalData = [];
          let systemData = [];
          let userData = [];
          let stealData = [];
          let iowaitData = [];
          for (var i in datas) {
            // var time = this.$tools.getTime(datas[i].time);
            totalData.push([
              datas[i].time,
              parseFloat((Number(datas[i].total) * 100).toFixed(3))
            ]);
            systemData.push([
              datas[i].time,
              parseFloat((Number(datas[i].system) * 100).toFixed(3))
            ]);
            userData.push([
              datas[i].time,
              parseFloat((Number(datas[i].user) * 100).toFixed(3))
            ]);
            stealData.push([
              datas[i].time,
              parseFloat((Number(datas[i].steal) * 100).toFixed(3))
            ]);
            iowaitData.push([
              datas[i].time,
              parseFloat((Number(datas[i].iowait) * 100).toFixed(3))
            ]);
          }
          this.cpuSeriesParam = [
            {
              //
              type: "line",
              name: "total(%)",
              data: totalData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            },
            {
              //
              type: "line",
              name: "system(%)",
              data: systemData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            },
            {
              //
              type: "line",
              name: "user(%)",
              data: userData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            },
            {
              //
              type: "line",
              name: "steal(%)",
              data: stealData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            },
            {
              //
              type: "line",
              name: "iowait(%)",
              data: iowaitData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            }
          ];
        })
        .catch(() => {});
    },
    ///SWAP
    getMemData() {
      var selectNodeId = this.selectNode;
      var param = {
        minutes: 60
      };
      this.$get("sys/mem/" + selectNodeId, param)
        .then(res => {
          var datas = res;
          let totalUsedData = [];
          let swapUsedData = [];
          for (var i in datas) {
            // var time = this.$tools.getTime(datas[i].time);
            totalUsedData.push([
              datas[i].time,
              parseFloat((datas[i].totalUsed / (1024 * 1024 * 1024)).toFixed(2))
            ]);
            swapUsedData.push([
              datas[i].time,
              parseFloat((datas[i].swapUsed / (1024 * 1024 * 1024)).toFixed(2))
            ]);
          }
          this.memSeriesParam = [
            {
              //
              type: "line",
              name: "(GB)",
              data: totalUsedData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            },
            {
              //
              type: "line",
              name: "swap(GB)",
              data: swapUsedData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            }
          ];
        })
        .catch(() => {});
    },
    //
    getDiskusageData() {
      var selectNodeId = this.selectNode;
      var param = {
        minutes: 60
      };
      this.$get("sys/diskusage/" + selectNodeId, param)
        .then(res => {
          if (
            res != null &&
            JSON.stringify(res) != "{}" &&
            JSON.stringify(res) != "[]"
          ) {
            this.diskusageList = res;
            this.diskusageName = [];
            for (var item in res) {
              this.diskusageName.push(item);
            }
            this.selectDiskusage = this.diskusageName[0];
            this.diskusageInit();
          }
        })
        .catch(() => {});
    },
    //
    diskusageInit() {
      var datas = this.diskusageList;
      for (var item in datas) {
        if (item == this.selectDiskusage) {
          var data = datas[item];
          this.diskSeriesParam = [];
          for (var i in data) {
            // var time = this.$tools.getTime(data[i].time);
            this.diskSeriesParam.push([
              data[i].time,
              parseFloat((data[i].usage * 100).toFixed(2))
            ]);
          }
        }
      }
    },
    //I/O
    getDiskioData() {
      var selectNodeId = this.selectNode;
      var param = {
        minutes: 60
      };
      this.$get("sys/diskio/" + selectNodeId, param)
        .then(res => {
          this.diskioList = res;
          this.diskioName = [];
          for (var item in res) {
            this.diskioName.push(item);
          }
          this.selectDiskio = this.diskioName[0];
          this.diskIOInit();
        })
        .catch(() => {});
    },
    //I/O
    diskIOInit() {
      var datas = this.diskioList;
      for (var item in datas) {
        if (item == this.selectDiskio) {
          var data = datas[item];
          this.diskIOSeriesData = [];
          for (var i in data) {
            // var time = this.$tools.getTime(data[i].time);
            this.diskIOSeriesData.push([
              data[i].time,
              parseFloat(Number(data[i].usage).toFixed(2))
            ]);
          }
        }
      }
    },
    //
    getNetData() {
      var selectNodeId = this.selectNode;
      // var selectNodeId="123";
      var param = {
        minutes: 60
      };
      this.$get("sys/net/" + selectNodeId, param)
        .then(res => {
          this.netList = res;
          this.netName = [];
          for (var item in res) {
            this.netName.push(item);
          }
          this.selectNet = this.netName[0];
          this.netInit();
        })
        .catch(() => {});
    },
    //
    netInit() {
      var datas = this.netList;
      if (Object.keys(datas).length != 0) {
        for (var item in datas) {
          if (item == this.selectNet) {
            var data = datas[item];
            let txBytesPsData = [];
            let rxBytesPsData = [];
            for (var i in data) {
              // var time = this.$tools.getTime(data[i].time);
              txBytesPsData.push([
                data[i].time,
                parseFloat((data[i].txBytesPs / 1024).toFixed(2))
              ]);
              rxBytesPsData.push([
                data[i].time,
                parseFloat((data[i].rxBytesPs / 1024).toFixed(2))
              ]);
            }
            this.netSeriesParam = [
              {
                //
                type: "line",
                name: "TX bytes(kb/s)",
                data: txBytesPsData,
                symbol: "emptyCircle",
                symbolSize: 6,
                showSymbol: true,
                cursor: "pointer"
              },
              {
                //
                type: "line",
                name: "RX bytes(kb/s)",
                data: rxBytesPsData,
                symbol: "emptyCircle",
                symbolSize: 6,
                showSymbol: true,
                cursor: "pointer"
              }
            ];
          }
        }
      } else {
        this.netSeriesParam = [
          {
            //
            type: "line",
            name: "TX bytes(kb/s)",
            data: [],
            symbol: "emptyCircle",
            symbolSize: 6,
            showSymbol: true,
            cursor: "pointer"
          },
          {
            //
            type: "line",
            name: "RX bytes(kb/s)",
            data: [],
            symbol: "emptyCircle",
            symbolSize: 6,
            showSymbol: true,
            cursor: "pointer"
          }
        ];
      }
    },
    //
    getLoad() {
      var selectNodeId = this.selectNode;
      // var selectNodeId="123";
      var param = {
        minutes: 60
      };
      this.$get("sys/load/" + selectNodeId, param)
        .then(res => {
          this.loadAverageSeriesData = [];
          for (var i in res) {
            // var time = this.$tools.getTime(res[i].time);
            this.loadAverageSeriesData.push([res[i].time, res[i].load]);
          }
        })
        .catch(() => {});
    },
    //
    refreshOperate() {
      this.nodeInfoInit();
    },
    initData() {
      this.cpuCores = null; //cpu
      this.cpuBrand = null; //cpu
      this.memoryTotalBytes = null; //
      this.osDesc = null; //
      this.cpuSeriesParam = [];
      this.memSeriesParam = [];
      this.diskusageName = [];
      this.diskusageList = null;
      this.diskSeriesParam = [];
      this.diskioList = null;
      this.diskIOSeriesData = [];
      this.netList = null;
      this.netSeriesParam = [];
      this.loadAverageSeriesData = [];
    },
    axisLabelFormatter: function(value, index) {
      var date = new Date(value);
      var texts = [
        date.getHours(),
        date.getMinutes() >= 10 ? date.getMinutes() : "0" + date.getMinutes()
      ];
      return texts.join(":");
    },
    text() {
      var ret = ""; //\n
      var maxLength = 2; //
      var valLength = value.length; //X
      var rowN = Math.ceil(valLength / maxLength); //
      if (rowN > 1) {
        //3,
        for (var i = 0; i < rowN; i++) {
          var temp = ""; //
          var start = i * maxLength; //
          var end = start + maxLength; //
          //
          temp = value.substring(start, end) + "\n";
          ret += temp; //
        }
        return ret;
      } else {
        return value;
      }
    }
  }
};
</script>


